.footer {
  background: #d3e5fe;
  padding: 60px 0 80px;
}

.footer__row {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.footer__title {
  color: #222;
  margin-bottom: 24px;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: 35px;
}

.footer__education {
  flex: 0 31.7%;
}

.footer__education__text {
  color: #555;
  margin-bottom: 32px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
}

.footer__education__media i {
  color: #086cf9;
  background: #fff;
  margin-right: 7px;
  font-size: 15px;
  transition: all .2s;
}

.footer__education__media i:hover {
  transform: scale(1.05);
  box-shadow: 0 1px 7px #0003;
}

.item1 i {
  padding: 12px 16px;
}

.item2 i {
  padding: 12px 13px;
}

.item4 i, .item3 i {
  padding: 12px 14px;
}

.footer__links {
  flex: 0 17.3%;
}

.footer__links__item {
  color: #444;
  margin-bottom: 13px;
  padding-left: 16px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  transition: color .1s linear;
  display: block;
  position: relative;
}

.footer__links__item:hover {
  color: #0047ab;
}

.footer__links__item:before {
  content: "";
  width: 6px;
  height: 6px;
  border-top: 2px solid #086cf9;
  border-left: 2px solid #086cf9;
  display: block;
  position: absolute;
  top: 37%;
  left: 0;
  transform: rotate(135deg);
}

.footer__support {
  flex: 0 10.8%;
}

.footer__contacts {
  flex: 0 18%;
}

.footer__contacts__address {
  margin-bottom: 16px;
}

.footer__contacts__item {
  color: #444;
  margin-bottom: 10px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.footer__contacts__item i {
  color: #222;
  margin-right: 9px;
  font-size: 15px;
}

@media (max-width: 1065px) {
  .footer__contacts__item {
    font-size: 13px;
  }

  .footer__contacts__item i {
    margin-right: 6px;
    font-size: 13px;
  }
}

@media (max-width: 860px) {
  .footer__title {
    margin-bottom: 20px;
    font-size: 26px;
    line-height: 30px;
  }
}

@media (max-width: 730px) {
  .footer {
    padding: 40px 0 45px;
  }

  .footer__title {
    margin-bottom: 16px;
    font-size: 20px;
    line-height: 24px;
  }

  .footer__education__text {
    margin-bottom: 17px;
    font-size: 13px;
    line-height: 16px;
  }

  .footer__education__media i {
    margin-right: 2px;
    font-size: 13px;
  }

  .item1 i {
    padding: 8px 12px;
  }

  .item2 i {
    padding: 8px 9px;
  }

  .item4 i, .item3 i {
    padding: 8px 10px;
  }

  .footer__links__item {
    margin-bottom: 7px;
    font-size: 13px;
    line-height: 16px;
  }

  .footer__contacts__address {
    font-size: 13px;
  }

  .footer__contacts__item {
    margin-bottom: 6px;
    font-size: 11px;
  }

  .footer__contacts__item i {
    margin-right: 4px;
    font-size: 12px;
  }
}

@media (max-width: 540px) {
  .footer {
    padding: 25px 0 30px;
  }

  .footer__row {
    max-width: 320px;
    flex-direction: column-reverse;
    align-items: center;
    margin: 0 auto;
  }

  .footer__title {
    font-size: 17px;
    font-weight: 500;
  }

  .none6 {
    display: none;
  }

  .footer__title {
    cursor: pointer;
    padding-left: 20px;
    position: relative;
  }

  .footer__title:hover {
    color: #0047ab;
  }

  .footer__title:before {
    content: "";
    width: 7px;
    height: 7px;
    border-top: 2px solid #0047ab;
    border-left: 2px solid #0047ab;
    display: block;
    position: absolute;
    top: 20%;
    left: 0;
    transform: rotate(135deg);
  }

  .footer__title:after {
    content: "";
    width: 100%;
    height: .5px;
    background-color: #555;
    margin: 15px auto;
    display: block;
  }

  .footer__links__title {
    margin-bottom: 23px;
  }

  .footer__education__media i {
    margin-right: 3px;
    font-size: 14px;
  }

  .item1 {
    margin-left: 16px;
  }
}

